.intro{
    width: 84%;
    margin: 10px auto;;
    display: flex;
    flex-direction: column;
    font-family: inherit;
    align-items: center;
    text-align: center;
}
.intro2{
    display: flex;
    flex-direction: column;
    margin: 5% 10%;
    align-items: center;
    text-align: center;
}
.download{
    margin-top: 3%;
    width:80%;
    color:black;
    background-color:  rgba(180, 201, 240, 0.979);
    padding: 10px;
    font-size: 18px;
    display: inline-block;
    border-radius: 20px;
    text-align: center;
}
.download:hover{
    background-color:rgba(202, 215, 240, 0.979);
    cursor:pointer;
}
.resume{
    font-family:inherit;
    font-size: 25px;
    text-decoration: underline;
    text-align: center;
}
.name{
    font-size: 20px;
    text-align: center;
}
.mail{
    text-align: center;
}
.linkedin{
    font-size: 15px;
    text-align: center;
    padding:6px ;
}
.github{
    text-align: center;
}
.intro_first{
    width: 90%;
    margin: 10px auto;
    padding: 10px auto;
    text-align: justify;
    font-size: 18px;
    align-items: center
}
.resume_intro{
    width: 94%;
    display: flex;
    flex-direction: column;
    margin:5px;
    padding:10px;
    color: rgba(0, 0, 0, 0.753);
}
.resume_parts{
    display: flex;
}
.resume_section{
    width: 50%;
    margin: 1%;
}
.intro_left_style{
    background-color: rgba(180, 201, 240, 0.979);
    border-radius: 10px;
    text-align: center;
    margin-top:50px;
    font-size: auto;
}
.intro_left_education{
    text-align:left;
    width: auto;
    height: auto;
}
.intro_right_style{
    background-color: rgba(180, 201, 240, 0.979);
    border-radius: 10px;
    text-align: center;
    margin-top: 50px;
    font-size: auto;
}
.intro_right_skills{
    text-align: left;
    width: auto;
    height: auto;
}
.intro_right_skills_style{
    padding: 10px;
    border-radius: 10px;
    background-color:rgba(216, 224, 238, 0.979);
    margin: 6px;
    display:inline-flex;
}
.resume_projects{
    width: 100%;
}
