.image{
    display: flex;
    flex-direction: column;
    align-items:center;
    text-align: center;
    height: auto;
    max-height:250px;
    min-width:100px;
    margin:auto;
    width:50%;
    font-style: italic;
}
.image_image{
    height: auto;
    width: 28%;
    border-radius: 50%;
}
.image_image:hover{
    height: 100px;
    width: 100px;
    box-shadow: 2px 2px 6px;
}
.image_title{
    margin-top: 10px;
    font-size: auto;
}