.home{   
    background-image: radial-gradient(rgb(223, 94, 143),rgb(95, 66, 148));
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
}
.home_head{   
    text-align: center;
    font: inherit;
    color: #ffffff;
    margin-top: 100px;
}
.home_headtext1{
    margin-top: 140px;
    font-family: "Source Sans Pro", Helvetica, sans-serif;
    font-weight: 300;
    font-size: 32pt;
    letter-spacing: -0.020em;
}
.home_headtext2{
    font-size: 12pt;  
}
.arrow{
    color: rgb(140, 221, 221);
}
.contact{
    color:black;
    background-color:  rgba(180, 201, 240, 0.979);
    padding: 15px;
    font-size: 18px;
    border-radius: 20px;
    display: inline-block;
    margin:20px auto;
    text-align: center;
}
.home_page{
    margin-top: 60px;
    background-color: rgb(247, 247, 247);
    width:80%;
    display:flex;
    flex-direction: column;
    align-items: center;
}
.home_page_per{
    width: 90%;
    display: inline-block;
}
.home_page0{
    background-color:#ffffff;
    display: flex;
    width: 100%;
    height: 50px;
    box-shadow: 2px 2px 4px grey;
    align-items: center;
}
.icon{
    color:rgb(95, 66, 148);
    margin:10px;
}
.icon:hover{
    border: 2px solid grey;
    padding: 5px;
    border-radius: 20px;
}
.home_page1{
    background-color:#ffffff;
    display: flex;
    width: 90%;
    height: auto;
    padding: 15px;
    margin: 35px 5%;
    box-shadow: 2px 2px 4px grey;
    align-items: center;
}
.home_page1_left{
    margin: 30px auto;
    width: 50%;
    font-family:inherit;
    text-align: justify;
    align-items: center;
    font-size: x-large;
}
.home_page1_text{
    margin-top: 50px;
    font-size: 18px;
}
.home_profile{
    height: auto;
    max-height: 290px;
    width: 26%;
    margin: 30px auto;
    border-radius: 50%;
    max-width: 50%;
}
.home_profile:hover{
    box-shadow: 6px 6px 8px rgb(172, 170, 170);
}
.home_page2{
    display: flex;
    flex-direction: column;
    width: 90%;
    height: auto;
    padding: 5px;
    margin: 25px 5%;
    box-shadow: 2px 2px 4px grey;
    background-color:#ffffff;
}
.home_page2_skill{
    font-family:inherit;
    text-align: center;
    font-size: xx-large;
    text-decoration: underline;
}
.home_page2_image{
    width: 100%;
    display: flex;
}
.home_page3{
    display: flex;
    flex-direction: column;
    width: 90%;
    height: auto;
    padding: 15px;
    margin: 35px 5%;
    box-shadow: 2px 2px 4px grey;
    background-color:#ffffff;
}
