.percentage_title{
    font-family: "Source Sans Pro", Helvetica, sans-serif;
    text-align: center;
    font-weight: 300;
    font-size: 32pt;
    letter-spacing: -0.020em;
    color: white;
    font-size: 25px;
    margin-top: 60px;
}
.percentage{
    display: flex;
}
.circle-wrap{
    width: 150px;
    height: 150px;
    margin:40px;
    margin-left: auto;
    height:100px;
    background: none;
    border-radius: 50%;
}
.circle-wrap .circle .mask,.circle-wrap .circle .fill,.circle-wrap .circle .fill2, .circle-wrap .circle .fill3,
.circle-wrap .circle .fill4,.circle-wrap .circle .fill5,.circle-wrap .circle .fill6 {
  width: 150px;
  height: 150px;
  position: absolute;
  border-radius: 50%;
  
}
.circle-wrap .circle .mask {
    clip: rect(0px, 150px, 150px, 75px);
    
}
.circle-wrap .circle .mask .fill,.circle-wrap .circle .mask .fill2,.circle-wrap .circle .mask .fill3,
.circle-wrap .circle .mask .fill4,.circle-wrap .circle .mask .fill5,.circle-wrap .circle .mask .fill6 {
    clip: rect(0px, 75px, 150px, 0px);
    background-color: rgb(240, 231, 213)
    
}
.circle-wrap .circle .mask.full,.circle-wrap .circle .fill{
    animation: fill ease-in-out 5s;
    transform: rotate(170deg);
    animation-iteration-count:infinite ;
    
}
  @keyframes fill {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(170deg);
    }
}
.circle-wrap .inside-circle {
    width: 130px;
    height: 130px;
    border-radius: 50%;
    background-image: radial-gradient(rgb(242, 159, 191),rgb(69, 29, 142));
    line-height: 130px;
    text-align: center;
    margin-top: 10px;
    margin-left: 10px;
    position: absolute;
    z-index: 100;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    font-size: 12px;
    color: rgb(247, 247, 247);
}
 

.circle-wrap .circle .mask.full2,.circle-wrap .circle .fill2{
      animation: fill2 ease-in-out 5s;
      transform: rotate(155deg);
      animation-iteration-count:infinite ;
}
    @keyframes fill2 {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(155deg);
      }
}

.circle-wrap .circle .mask.full3,.circle-wrap .circle .fill3{
    animation: fill3 ease-in-out 5s;
    transform: rotate(155deg);
    animation-iteration-count:infinite ;
}
  @keyframes fill3 {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(155deg);
