}

.circle-wrap .circle .mask.full4,.circle-wrap .circle .fill4{
    animation: fill4 ease-in-out 5s;
    transform: rotate(110deg);
    animation-iteration-count:infinite ;
}
  @keyframes fill4 {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(110deg);
