}

.circle-wrap .circle .mask.full5,.circle-wrap .circle .fill5{
    animation: fill5 ease-in-out 5s;
    transform: rotate(140deg);
    animation-iteration-count:infinite ;
}
  @keyframes fill5 {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(140deg);
    }
}
.circle-wrap .circle .mask.full6,.circle-wrap .circle .fill6{
  animation: fill6 ease-in-out 5s;
  transform: rotate(125deg);
  animation-iteration-count:infinite ;
}
@keyframes fill6 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(125deg);
  }
}